html, body {
  background: #111;
  margin: 0;
  padding: 0;
  font-family: Montserrat, serif;
  overflow: hidden;
}

.constellation-wrapper {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.constellation-wrapper .header {
  width: 100vw;
  height: 60px;
  background: #000c;
  padding: 10px 30px;
  position: fixed;
  top: 0;
  left: 0;
}

.constellation-wrapper .header h1 {
  color: #fff;
  float: left;
  font-size: 25px;
  line-height: 40px;
}

.constellation-wrapper .header button {
  float: right;
  margin-left: 10px;
}

.constellation-wrapper .source {
  color: #fff;
  height: 35px;
  text-align: right;
  background: #000c;
  padding: 8px 20px;
  font-size: 15px;
  position: fixed;
  bottom: 0;
  right: 0;
}

.constellation-wrapper .source a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
}

.constellation-wrapper #constellation {
  width: 100%;
  height: 100%;
}

